exports.push([module.id, "\n#map, [id^=\"map\"], [id$=\"map\"] {\n  min-height: 66vmin !important;\n}\n", "", {"version":3,"sources":["/Users/z00221y/.bitnami/stackman/machines/xampp/volumes/root/htdocs/clients/ws3/wp-content/themes/ws-ui/src/views/page/src/views/page/index.vue"],"names":[],"mappings":";AAoEA;EACA,8BAAA;CACA","file":"index.vue?vue&type=style&index=0&lang=css&","sourcesContent":["<template>\n\n<AppLoad\n  v-if=\"page.loading\"\n  />\n<PageBuilder :panels=\"acf.panels\" :id=\"$route.name\" v-else/>\n\n</template>\n<script>\nimport API from \"@/VuePress/mix/API\"\nimport PageBuilder from \"@/components/modules/PageBuilder\"\n\nexport default {\n  name: \"Page\",\n  mixins:[ API ],\n  components:{ PageBuilder },\n  props:{\n    slug: \"\",\n    fetch:{\n      type: Function,\n      default(WP){\n        let ept = WP.pages()\n        if( this.slug )\n          ept = ept.slug(this.slug)\n        return ept\n      }\n    }\n  },\n  computed:{\n    endpoint(){\n      if(!( this.API && this.fetch )) return false\n      else return this.fetch(this.API)\n    },\n    acf(){\n      if( this.page.loading ) return\n      return this.page.acf\n    },\n    title(){\n      if( this.page.loading ) return ''\n      return this.page.title.rendered\n    },\n    excerpt(){\n      if( this.page.loading ) return ''\n      return this.page.excerpt.rendered\n    },\n    content(){\n      if( this.page.loading ) return ''\n      return this.page.content.rendered\n    },\n  },\n  asyncComputed:{\n    page:{\n      default:{ loading:true },\n      async get(){\n        if(!( this.API && this.endpoint )) return {loading:true}\n        let\n        err = false,\n        xhr = await this.endpoint.get().catch(e=>( err = e ))\n\n        if (err) return { error:err }\n        else return xhr[0]\n      }\n    }\n  }\n}\n</script>\n\n<style>\n#map, [id^=\"map\"], [id$=\"map\"] {\n  min-height: 66vmin !important;\n}\n</style>\n"],"sourceRoot":""}]);

